      <van-panel>
         <div class="title">我的钱包</div> 
           <van-cell class="item" id="fil_time_1" @click="$router.push({ name: 'bonus', query: { code: '153F39158E424AD79193270BC6FEC468',name: '红包余额' }  })">
              <p  v-for="(item,index) in accountList" :key="index" ><span class="price" v-if="item.ptCode=='153F39158E424AD79193270BC6FEC468' ">{{item.acAmt}}</span></p> 
              <span>红包余额</span>
           </van-cell>
           <van-cell class="item" id="fil_time_2" @click="$router.push({ name: 'bonus', query: { code: '1202763432698138624',name: '福利资产' }  })">
              <p  v-for="(item,index) in accountList" :key="index" ><span class="price" v-if="item.ptCode=='1202763432698138624' ">{{item.acAmt}}</span></p> 
              <span>福利资产</span>
           </van-cell>
           <van-cell class="item" id="fil_time_3"  @click="$router.push({ name: 'bonus', query: { code: '-1',name: '三农通证' }  })">
              <p  v-for="(item,index) in accountList" :key="index" ><span class="price" v-if="item.ptCode=='-1' ">{{item.acAmt}}</span></p> 
              <span>三农通证</span>
           </van-cell>
           <van-cell class="item" id="fil_time_4"  @click="$router.push({ name: 'bonus', query: { code: '-2',name: '助农基金' }  })">
              <p  v-for="(item,index) in accountList" :key="index" ><span class="price" v-if="item.ptCode=='-2' ">{{item.acAmt}}</span></p> 
              <span>助农基金</span>
           </van-cell>   
      </van-panel> 
    </div>
    <div class="com1">
      <van-panel>
        <p class="title">我的订单</p>  
        <van-cell class="item" title="待付款"  @click="$router.push({ name: 'orderList', query: { index: 1 } })">
          <van-icon class="tu" name="arrow" />
        </van-cell>
        <van-cell class="item" title="待发货"  @click="$router.push({ name: 'orderList', query: { index: 2 } })" >
          <van-icon class="tu" name="arrow" />
        </van-cell>
        <van-cell class="item" title="待收货" @click="$router.push({ name: 'orderList', query: { index: 3 } })"  >
          <van-icon class="tu" name="arrow" />
        </van-cell>
        <van-cell class="item" title="已完成" @click="$router.push({ name: 'orderList', query: { index: 4 } })" >
          <van-icon class="tu" name="arrow" />
        </van-cell>   
      </van-panel>
    </div>
  </div>
</template>
<script>
import { getPersonalInfo, getPersonalBalance,getUserLevel, wxApi } from "@/api";
import { Cell, CellGroup, Icon } from "vant";
// import { getCartList, reviseCartNumber, deleteCart } from "@/api";
import MeScroll from "mescroll.js";
export default {
  components: {
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
  },
  // components: {
  //   [SwipeCell.name]: SwipeCell,
  //   [SwitchCell.name]: SwitchCell,
  //   [Dialog.name]: Dialog
  // },
  data() {
    return {
      realName: "",
      headPic: "",
      userCode: "",
      empType: 0,
      encourage: 0,
      voucher: 0,
      balance: 0,
      userLevel: 1,
      userLevelName: "",
      accountList: ''
    };
  },
  methods: {
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true, // 禁用背景点击
      });
      getPersonalInfo().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.realName = res.data.userName;
          this.headPic = res.data.userHeader;
          this.empType = parseInt(res.data.empType);
          this.userLevel = parseInt(res.data.userLevel);
          sessionStorage.setItem("userCode", res.data.userCode);
        } else {
          this.$toast.fail(res.msg);
        }
      });
      getPersonalBalance().then(res => {
        if (res.code == 0) {
          this.accountList = res.data;
          this.$toast.success(res.msg);
        } else {
          this.$toast.fail(res.msg);
        }
        console.log(res.data)
      });
      getUserLevel().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.userLevelName = res.data; 
        } else {
          this.$toast.fail(res.msg);
        }
      });
    },
  },
  mounted() {
    document.title = "钱包";
    this.loadData();
  },
};
</script>
